elf.nativeStyles.define('textarea', 'textarea{display:none;-webkit-appearance:none;-moz-appearance:none;appearance:none;text-align:left;font-family:inherit;font-size:12rem;font-weight:400;height:24px;width:152px;max-width:100%;padding:0 8px;margin:1px 0;outline:0;box-sizing:border-box;display:inline-flex;align-items:center;position:relative;vertical-align:middle;color:#ccc;border-radius:0;background-color:#0d0d0d;text-overflow:ellipsis;border:1px solid #404040;resize:none;line-height:1.2;padding:calc((24px - 1px * 2 - 1em * 1.2)/ 2) 8px}textarea[disabled]{color:rgba(204,204,204,.5);border:1px solid rgba(64,64,64,.5)}textarea[type=number]{padding-right:0}textarea[warning]{border:1px solid #7f6400}textarea[warning][disabled]{color:rgba(204,204,204,.5);border:1px solid rgba(127,100,0,.5)}textarea[error],textarea[error][warning]{border:1px solid #a01c2b}textarea[error][disabled],textarea[error][warning][disabled]{color:rgba(204,204,204,.5);border:1px solid rgba(160,28,43,.5)}textarea ::-ms-clear{display:none}textarea[cols]{width:auto}textarea[rows]{height:auto}textarea:focus{background-size:100% 2px,100% 1px!important;background-position:center bottom!important;border-color:#334bff}');
